import React from "react";
import { Nav } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { useNavigate } from "react-router-dom";
import need4sms from "../../../img/perl-cgi-collection-of-jokes-need4sms.png";
import ScrollToTop from './../../ScrollToTop/ScrollToTop';


const Need4sms = () => {
  const navigate = useNavigate();
  return (
    <React.Fragment>
      <ScrollToTop />
      <div className="container">
        <span id="nyhetsfelt"></span>
        <h1 className="text-start">Skbart SMS-vitsearkiv i Perl</h1>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="subpage_container w-100">
                  <div className="row">
                    <div className="col-md-12">
                      <div className="wordBreakWrapper">
                        <div className="wordBreakImage w-50">
                          <a href={need4sms} target="_blank" rel="noopener noreferrer" title="pne bildet i ny fane">
                            <img
                              className="img-thumbnail"
                              src={need4sms}
                              alt="Eksempel av nettside"
                            />
                          </a>
                        </div>
                        <div className="wordBreakText">
                          <p className="text-start">
                          I perioden 2001-2003 bygde jeg et CMS i Perl for  organisere og lagre innsendte SMS-vitser. Jeg utvidet prosjektet til et skbart arkiv med over 1 100 kategoriserte gratis sms-vitser, sjekkereplikker og dynamiske SMS. Prosjektet inkluderte muligheten for brukere  sende inn egne vitser via skjemaer, som administrator godkjente og publiserte via kun et trykk p en knapp. Nettsiden tiltrakk seg over 10 000 unike beskende per mned, og ble et av Norges strste gratis SMS-arkiv p den tiden.
                          </p>
                          <p className="text-start">
                          Prosjektet gav meg innsikt i hvordan man kan hndtere store mengder tekstdata og gjre det lett tilgjengelig for brukere gjennom en skbar database. Jeg lrte mye om effektiv databasestyring og brukerinteraksjon, samt hvordan man kan optimalisere innholdet for skemotorer, noe som frte til hy trafikk p nettstedet. Brukerne kunne enkelt navigere i det store arkivet, noe som kte engasjementet.
                          </p>
                          <p className="text-start">
                          Nettsiden ble da et av de mest populre bibliotekene for SMS-vitser, og det lrte meg mye om  bygge skalerbare og brukervennlige lsninger. Prosjektet gav meg verdifull erfaring med webutvikling og databasedesign. Etter et sikkerhetsbrudd mtte siden til slutt legges ned, noe som lrte meg viktigheten av sikkerhetstiltak og regelmessige sikkerhetskopier - en praksis jeg har viderefrt i alle mine senere prosjekter.
                          </p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12 backLink">
                      <React.Fragment>
                        <Nav.Link className="link-primary" onClick={() => navigate(-1)}>
                          G tilbake
                        </Nav.Link>
                      </React.Fragment>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Need4sms;