import React from "react";
import { Nav } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import quiz from "../../../img/php-bootstrap-quiz.png";
import ScrollToTop from './../../ScrollToTop/ScrollToTop';

const Quiz = () => {
  const navigate = useNavigate();
  return (
    <React.Fragment>
      <ScrollToTop />
      <div className="container">
        <span id="nyhetsfelt"></span>
        <h1 className="text-start">Kundetilfredshet: Sprreunderskelse</h1>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="subpage_container w-100">
                  <div className="row">
                    <div className="col-md-12">
                      <div className="wordBreakWrapper">
                        <div className="wordBreakImage w-25">
                          <a href={quiz} target="_blank" rel="noopener noreferrer" title="pne bildet i ny fane">
                            <img
                              className="img-thumbnail"
                              src={quiz}
                              alt="Eksempel p redigering av sprreunderskelse"
                            />
                          </a>
                        </div>
                        <div className="wordBreakText">
                          <p className="text-start">
                            I et CMS som hadde vrt i drift i over 10 r uten noen form for kundeunderskelse, tok jeg initiativ til  bruke en egenutviklet skjemabygger for  gjennomfre en sprreunderskelse til innsamling av tilbakemeldinger fra brukerne. Jeg utformet sprsmlene basert p en 0-5-skala for  sikre konsistente og sammenlignbare statistikker over tid. For  ke deltakelsen ble det ogs lagt til en premiering med trekning av en iPhone.
                          </p>
                          <p className="text-start">
                            Underskelsen gav oss innsikt i viktige omrder som brukernes opplevelse og utfordringer med plattformen. Denne informasjonen ble brukt til  gjre mlrettede forbedringer, som igjen bidro til kt kundelojalitet og redusert kundefrafall.
                          </p>
                          <p className="text-start">
                            P bakgrunn av underskelsen utviklet vi forbedringer i redigeringsgrensesnittet, blant annet ved  redusere antall klikk fra fem til ett og innfre inline-redigering. Dette bidro til kt brukervennlighet og reduserte med all sannsynlighet kundefrafall med 5% over de neste 3 rene. I tillegg frte innsikten fra underskelsen til kt mersalg, noe som gjorde plattformen mer lnnsom og konkurransedyktig.
                          </p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12 backLink">
                      <React.Fragment>
                        <Nav.Link className="link-primary" onClick={() => navigate(-1)}>
                          G tilbake
                        </Nav.Link>
                      </React.Fragment>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Quiz;
